/* General colours */
.single-module {
  border: 1px solid grey;
  background: white;
  padding: 15px;
  margin: 0 15px 15px 0;
  background: #ffffff; }
  .single-module .module-title--heading {
    display: inline-block; }
  .single-module .module-title--switch {
    float: right;
    margin: 1.33em 0; }
  .single-module .module-settings fieldset legend {
    text-decoration: underline;
    margin-bottom: 15px; }
  .single-module .module-settings fieldset label {
    display: block;
    margin-bottom: 25px; }
  .single-module .module-settings fieldset p {
    display: block;
    margin-bottom: 25px; }
  .single-module .module-settings fieldset button {
    background: #0073aa;
    color: #ffffff;
    border: none;
    padding: 5px 15px; }
    .single-module .module-settings fieldset button:hover {
      background: #0085c4; }
    .single-module .module-settings fieldset button:focus {
      outline: none; }
    .single-module .module-settings fieldset button:disabled {
      background: #f1f1f1;
      color: #a4a4a4; }
